import React, {  useState } from 'react';
import { RootStateOrAny, useDispatch, useSelector } from 'react-redux';
import { PadWalletAddress } from 'utils/padwallet';
import { Link, useHistory } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useKaiECOMarketplaceContract } from 'hooks/useContract';
import { getKaiECOMarketplaceAddress } from 'utils/addressHelpers';
import { useWallet } from '@binance-chain/bsc-use-wallet';
import CountDown from 'components/CountDown/CountDown';
import { symbol } from 'utils/symbol';
import { decimal } from 'utils/decimal';
import { getBalanceNumber } from 'utils/formatBalance';

const initData = {
    pre_heading: "Auctions",
    heading: "Live Auctions",
    content: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Laborum obcaecati dignissimos quae quo ad iste ipsum officiis deleniti asperiores sit.",
    btnText: "Load More"
}



function AuctionAll() {
    const tokens = useSelector((state: RootStateOrAny) => state.marketplace.items)
    const auctions = tokens.filter(token =>token.auctionTime > (new Date().getTime() / 1000).toFixed());
    const dispatch = useDispatch();
    const {account} = useWallet();
    const marketplaceContract = useKaiECOMarketplaceContract(getKaiECOMarketplaceAddress());
    const [contents, setContents] = useState(initData);
    const [tokenData, setTokenData] = useState(null);
    const [bidPrice, setBidPrice] = useState(0);
    const SetItemData = (contract, tokenId) => {
        let data = auctions.filter(token => token.contract === contract).find(token => token.tokenId === tokenId);
        setTokenData(data);
    }
    const PlaceOnBid = async () => {
        const collection = tokenData?.contract;
        const itemId = tokenData?.itemId;
        const createTime = new Date().getTime() / 1000;
        marketplaceContract.methods.placeOnBid(collection, itemId, bidPrice, createTime.toFixed()).send({ from: account })
            .on('error', (error) => {
                toast.error(error, {
                    position: "top-right",
                    autoClose: 3000,
                    theme: 'dark',
                    pauseOnHover: false
                });
                dispatch({ type: 'CHANGE_LOAD', payload: false })
            })
            .on('transactionHash', (hash) => {

                toast.success(hash, {
                    position: "top-right",
                    autoClose: 3000,
                    theme: 'dark',
                    pauseOnHover: false
                });
            });

    }
    return(
        <section className="live-auctions-area">
            <div id="PlaceOnBid" className="modal fade p-0" >
                <div className="modal-dialog dialog-animated">
                    <div className="modal-content h-100">
                        <div className="modal-header" data-dismiss="modal">
                            Place On Bid <i className="far fa-times-circle icon-close" />
                        </div>
                        <div className="menu modal-body">
                            <div className="row align-self-center">

                                <div className="col-12">
                                    <div className="form-group">
                                        <input type="number" className="form-control" name="name" placeholder="Bid Price" required={true} onChange={e => setBidPrice(parseInt(e.target.value))} />
                                    </div>
                                </div>
                                <div className="col-12">
                                    <button className="btn w-100 mt-3 mt-sm-4" type="button" onClick={PlaceOnBid} >Place On Bid</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-12">
                        {/* Intro */}
                        <div className="intro d-flex justify-content-between align-items-end m-0">
                            <div className="intro-content">
                                <span>{contents.pre_heading}</span>
                                <h3 className="mt-3 mb-0">{contents.heading}</h3>
                            </div>
                            
                        </div>
                    </div>
                </div>
                <div className="row items">
                    {auctions.map((item, idx) => {
                        return (
                            
                            <div key={`auct_${idx}`} className="col-12 col-sm-6 col-lg-3 item">
                                <div className="card">
                                    <div className="image-over">
                                        <Link to={`/token/${item.contract}/${item.tokenId}`}><img className="card-img-top" src={item.image} alt="" /></Link> 
                                    </div>
                                    {/* Card Caption */}
                                    <div className="card-caption col-12 p-0">
                                        {/* Card Body */}
                                        <div className="card-body">
                                            <CountDown autionTime = {item?.auctionTime} />
                                            <Link to={`/token/${item?.contract}/${item?.tokenId}`}>
                                                <h5 className="mb-0">{item.name}</h5>
                                            </Link>
                                           
                                            <a className="seller d-flex align-items-center my-3" href="/author">
                                                <img className="avatar-sm rounded-circle" src={'/img/user.jpg'} alt="" />
                                                <span className="ml-2">{PadWalletAddress(item?.owner)}</span>
                                            </a>
                                            <div className="card-bottom d-flex justify-content-between">
                                                <span>{ getBalanceNumber(item.price, decimal(item.token)) } {symbol(item.token)}</span>
                                                <span>{1}</span>
                                            </div>
                                            {item?.owner != account &&  <a className="btn btn-bordered-white btn-smaller mt-3" href="/#" data-toggle="modal" data-target="#PlaceOnBid" onClick={() => SetItemData(item?.contract, item?.tokenId)}><i className="icon-handbag mr-2" />{'Place On Bid'}</a>}
                                           
                                        </div>
                                    </div>
                                </div>
                            </div>
                        );
                    })}
                </div>
                {/* <div className="row">
                    <div className="col-12 text-center">
                        <a id="load-btn" className="btn btn-bordered-white mt-5" href="#">{initData.btnText}</a>
                    </div>
                </div> */}
            </div>
            <ToastContainer />
        </section>
    )
}


export default AuctionAll;