import React, {  useEffect, useState } from 'react';
import { RootStateOrAny, useDispatch, useSelector } from 'react-redux';
import { PadWalletAddress } from 'utils/padwallet';
import { Link, useHistory } from 'react-router-dom';

const CountDown = (props) => {
    const auctionTime = props.auctionTime;
   
    
    const calculateTimeLeft = () => {
        let difference = auctionTime - new Date().getTime() / 1000;
        let timeLeft = {};
        
        if (difference > 0) {
          timeLeft = {
            days: Math.floor(difference / ( 60 * 60 * 24)),
            hours: Math.floor((difference / ( 60 * 60)) % 24),
            minutes: Math.floor((difference  / 60) % 60),
            seconds: Math.floor((difference ) % 60)
        };
      }
    
      return timeLeft;
    
    }

    const [timeLeft, setTimeLeft] = useState(null);
    
    useEffect(() => {
        setTimeLeft(calculateTimeLeft());
        const interval = setInterval(() => {
            setTimeLeft(calculateTimeLeft());
        }, 1000);
        return () => clearInterval(interval);
    }, [])
    return(
        <div className="countdown-times mb-3">                                            
            <div className="countdown d-flex justify-content-center"  >
                <div className='countdown-container days'>
                    <span className='countdown-heading days-top'>Days</span>
                    <span className='countdown-value days-bottom'>{timeLeft?.days}</span>
                </div>
                <div className='countdown-container hours'>
                    <span className='countdown-heading hours-top'>Hours</span>
                    <span className='countdown-value hours-bottom'>{timeLeft?.hours}</span>
                </div>
                <div className='countdown-container minutes'>
                    <span className='countdown-heading minutes-top'>Minutes</span>
                    <span className='countdown-value minutes-bottom'>{timeLeft?.minutes}</span>
                </div>
                <div className='countdown-container seconds'>
                    <span className='countdown-heading seconds-top'>Seconds</span>
                    <span className='countdown-value seconds-bottom'>{timeLeft?.seconds}</span>
                </div>
            </div>
        </div>
    )
}

export default CountDown;